.card {
 
  border-radius: 4px;
  height: 170px;
  margin-left: auto;
  margin-bottom: 10px;
  /* position: relative; */
  width: 200px;
  -webkit-box-shadow: inset 0 0 3px 2px #999, 0 3px 6px rgb(77, 75, 75);
          box-shadow: inset 0 0 3px 2px #999, 0 3px 6px rgb(77, 75, 75);
  
}

.card > .img-thumbnail {
  height: 100%;
  overflow: hidden;

}
.card > .img-thumbnail > img {
  width: 100%
}

.card > .content {
  padding: 1rem;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

img{
  height: 160px;
  width: 200px;
  overflow: hidden;
}

@media screen and (max-width: 1024px) {
  /* STYLES HERE */
  img{
    height: 120px;
    width: 140px;
  }

  .card {
    height: 130px;
    width: 160px;
  }
  
}

@media screen and (max-width: 760px) {
  /* STYLES HERE */
  img{
    height: 89px;
    width: 90px;
  }

  .card {
    height: 100px;
    width: 120px;
  }

  span{
    font-size: 16px;
  }
  
  
}


